<template>
  <div class="information block_center">
    <header>
      <div>
        <h1 class="title">ICO</h1>
      </div>
    </header>
    <section class="bg_white info-overview">
      <h2 class="sec-title"></h2>
      <div class="button-group">
        <button v-bind:class="{ 'info-active': isActive=='global' }" @click="getInfo('')"></button>|
        <button v-bind:class="{ 'info-active': isActive=='china' }" @click="getInfo()"></button>|
        <button v-bind:class="{ 'info-active': isActive=='other' }" @click="getInfo(1)"></button>
      </div>
      <article class="item-list">
        <div v-for="item in information" class="item">
          <span class="name">{{item.name}}</span>
          <aside>
            <span class="num">{{item.number}}</span>
            <span class="unit">{{item.unit}}</span>
          </aside>
        </div>
      </article>
    </section>
    <section class="ico-num">
      <h2 class="sec-title">ICO</h2>
      <article class="index">
        <div class="left">
          <div id="myChart" style="height:400px;"></div>
          <p class="x-axis">
            <span v-for="item in xAxis">{{item}}</span>
          </p>
        </div>
        <div class="right">
          <div class="danger">
            <section>
              <span class="cur-index">:</span>
              <div class="num">{{icoScore}}</div>
              <span></span>
            </section>
            <section>
              <span class="sub-title">ICO</span>
              <span class="tips">icoico</span>
            </section>
          </div>
          <div class="data">
            <div v-for="item in allIco" class="item">
              <span class="title">{{item.title}}<i class="sub">{{item.subTitle}}</i></span>
              <span class="number">{{item.number}}</span>
            </div>
          </div>
        </div>
      </article>
    </section>
    <section class="risk">
      <h2 class="sec-title"></h2>
      <article class="main_width item-list">
        <div v-for="item in risk" class="item">
          <img v-bind:src="item.img" />
          <span class="number">{{item.number}}<i></i></span>
          <span class="name">{{item.name}}</span>
          <div class="bg"></div>
        </div>
      </article>
    </section>
    <section>
      <h2 class="sec-title"></h2>
      <article class="main_width report">
        <div v-for="item in report" class="item" @click="download(item.industry_url)">
          <span class="time">{{item.updated_at | date}}</span>
          <span class="content">{{item.industry_name}}</span>
        </div>
      </article>
    </section>
  </div>
</template>

<script>
import api from '../api/index'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/line'
export default {
  name: 'information',
  data () {
    return {
      sign: 'token=0',
      icoScore: null,
      xAxis: ['2', '3', '4', '5'],
      yAxis: [5, 8, 4, 7],
      isActive: 'china',
      allIco: [
        { title: 'ICO', sign: 'ico_score', number: '' },
        { title: 'ICO', sign: 'ico_num', number: '' },
        { title: 'ICO', sign: 'ico_amount', number: '', subTitle: '()' }
      ],
      information: [
        {name: 'ICO', number: '', unit: '', sign: 'ico_num'},
        {name: 'ICO', number: '', unit: '', sign: 'ico_amount'},
        {name: 'ICO', number: '', unit: '', sign: 'user'},
        {name: '', number: '', unit: '', sign: 'high_grade_num'},
        {name: '', number: '', unit: '', sign: 'high_risk_num'},
        {name: '', number: '', unit: '', sign: 'cheat_num'}
      ],
      risk: [
        {img: require('@/assets/images/information/info1.png'), name: '', number: '', sign: 'code_safe_num'},
        {img: require('@/assets/images/information/info2.png'), name: '', number: '', sign: 'team_strength_num'},
        {img: require('@/assets/images/information/info3.png'), name: '', number: '', sign: 'business_model_num'},
        {img: require('@/assets/images/information/info4.png'), name: '', number: '', sign: 'web_info_num'},
        {img: require('@/assets/images/information/info5.png'), name: 'ICO', number: '', sign: 'white_paper_num'},
        {img: require('@/assets/images/information/info6.png'), name: '', number: '', sign: 'public_info_num'}
      ],
      report: []
    }
  },
  created: function () {
    Promise.all([this.getInfo(), this.getReport(), this.getRisk(), this.getIcoInfo()])
  },
  mounted () {
    this.drawLine(this.yAxis)
  },
  methods: {
    getInfo: function (belong = 0) {
      let data = `${this.sign}&belong=${belong}`

      switch (belong) {
        case 0:
          this.isActive = 'china'
          break
        case 1:
          this.isActive = 'other'
          break
        case '':
          this.isActive = 'global'
          break
        default:
          break
      }

      api.post('publish/info', {sign: data}).then((res) => {
        for (let i = 0, len = this.information.length; i < len; i++) {
          let sign = this.information[i].sign
          if (sign === 'user') {
            this.information[i].number = '105873'
          } else {
            this.information[i].number = res[sign]
          }
        }
      }).catch(error => {
        console.log(error)
      })
    },

    getReport: function () {
      api.post('publish/report', {sign: this.sign}).then((res) => {
        this.report = res
      }).catch(error => {
        console.log(error)
      })
    },

    getRisk: function () {
      api.post('publish/risk', {sign: this.sign}).then((res) => {
        for (let i = 0, len = this.risk.length; i < len; i++) {
          let sign = this.risk[i].sign
          this.risk[i].number = res[sign]
        }
      }).catch(error => {
        console.log(error)
      })
    },

    getIcoInfo: function () {
      api.post('home/ico_list', { sign: 'token=0' })
      .then((res) => {
        this.icoScore = res.ico_score
        for (let i = 0, len = this.allIco.length; i < len; i++) {
          let sign = this.allIco[i].sign
          this.allIco[i].number = res[sign]
        }
      }).catch(error => {
        console.log(error)
      })
    },

    drawLine (data) {
      let myChart = echarts.init(document.getElementById('myChart'))
      myChart.setOption({
        title: {
          text: 'ICO'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: '-4%',
          right: '0',
          bottom: '5%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            show: false,
            data: []
          }
        ],
        yAxis: [
          {
            show: false
          }
        ],
        series: [
          {
            name: 'ICO',
            type: 'line',
            areaStyle: {

            },
            data: data,
            lineStyle: {
              normal: {
                color: '#fff'
              }
            },
            showSymbol: false,
            smooth: true
          }
        ]
      })
    },

    download (url) {
      if (!url) return
      window.open(url)
    }
  },
  filters: {
    date: function (value) {
      if (!value) return ''
      let start = value.indexOf('-')
      let end = value.indexOf(' ')
      return value.substring(start + 1, end)
    }
  }
}
</script>

<style lang='scss' scoped>
  @import '../assets/css/information.scss';
</style>


